"use client";

import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from "@/components/ui/card";
import { useLanguage } from "@/contexts/LanguageContext";
import servicesData from "@/lib/serevices.json";
import * as Icons from "lucide-react";
import Link from "next/link";
import { Button } from "@/components/ui/button";
import { ArrowRight } from "lucide-react";
import ScrollReveal from "@/components/ui/ScrollReveal";

const ServicesSection = () => {
  const { t } = useLanguage();

  const getIcon = (iconName: string) => {
    // @ts-ignore - Dynamic icon access
    const Icon = Icons[iconName as keyof typeof Icons];
    return Icon ? <Icon className="w-10 h-10 mb-4 text-primary" /> : null;
  };

  return (
    <section id="services" className="py-24 bg-secondary/30">
      <div className="container px-4 mx-auto">
        <ScrollReveal width="100%" className="text-center mb-16">
          <h2 className="text-4xl font-bold mb-4 text-foreground">{t.services.title}</h2>
          <p className="text-lg text-muted-foreground max-w-2xl mx-auto">
            {t.services.subtitle}
          </p>
        </ScrollReveal>

        <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-8">
          {servicesData.services.map((service, index) => (
            <ScrollReveal
              key={service.id}
              delay={index * 0.1}
              width="100%"
              viewportAmount={0.2}
            >
              <Card className="h-full hover:shadow-elevated transition-all duration-300 hover:scale-[1.02] hover:border-primary/50 group bg-card border-transparent shadow-card">
                <CardHeader>
                  <div className="transform transition-transform duration-300 group-hover:-translate-y-1">
                    {getIcon(service.icon)}
                  </div>
                  <CardTitle className="text-xl mb-2 text-foreground group-hover:text-primary transition-colors">
                    {service.title[t.locale as keyof typeof service.title]}
                  </CardTitle>
                  <CardDescription className="text-muted-foreground">
                    {service.shortDescription[t.locale as keyof typeof service.shortDescription]}
                  </CardDescription>
                </CardHeader>
                <CardContent>
                  <ul className="space-y-2">
                    {service.features[t.locale as keyof typeof service.features].slice(0, 3).map((feature, i) => (
                      <li key={i} className="flex items-center text-sm text-foreground/80">
                        <Icons.Check className="w-4 h-4 mr-2 text-accent" />
                        {feature}
                      </li>
                    ))}
                  </ul>
                </CardContent>
                <CardFooter>
                  <Link href={`/services/${service.id}`} className="w-full">
                    <Button variant="ghost" className="w-full justify-between group-hover:text-primary group-hover:bg-primary/5">
                      {t.services.learnMore} <ArrowRight className="w-4 h-4 ml-2 transition-transform group-hover:translate-x-1" />
                    </Button>
                  </Link>
                </CardFooter>
              </Card>
            </ScrollReveal>
          ))}
        </div>
      </div>
    </section>
  );
};

export default ServicesSection;
